<template>
  <div class="container mx-auto h-screen flex items-center justify-center ">
    <div class="flex h-screen items-center justify-center" v-if="loading">
      📚 Loading...
    </div>
    <div class="px-2 flex flex-col md:flex-row w-full" v-else>
      <div class="md:w-1/2 flex justify-center items-center relative ">
        <h1
          class="text-3xl font-extrabold text-center absolute top-1 md:top-16 text-purple-800"
        >
          📚 justSpell
        </h1>
        <button @click="speak(word)" class="speak-btn">
          🔊
        </button>
      </div>
      <div class="md:w-1/2">
        <div class="flex flex-row-reverse justify-center md:justify-start">
          <select
            name="number_of_letters"
            id="numberOfLeters"
            class="px-2 rounded"
            v-model="numberOfLetters"
            @change="loadNewWord"
          >
            <option value="">Random</option>
            <option value="3">3 letters</option>
            <option value="4">4 letters</option>
            <option value="5">5 letters</option>
            <option value="6">6 letters</option>
            <option value="7">7 letters</option>
            <option value="8">8 letters</option>
            <option value="9">9 letters</option>
            <option value="10">10 letters</option>
            <option value="11">11 letters</option>
            <option value="12">12 letters</option>
            <option value="13">13 letters</option>
            <option value="14">14 letters</option>
            <option value="15">15 letters</option>
          </select>
        </div>

        <div class="flex my-10 justify-center">
          <div
            class="text-container"
            v-for="(letter, index) in wordHidden"
            :key="index"
          >
            {{ letter }}
          </div>
        </div>
        <div class="word-description-container">
          <div class="my-8">
            <p class="text-gray-500 mb-2 font-bold text-sm">Definition:</p>
            <p>
              {{ definition }}
            </p>
          </div>
          <div class="my-8">
            <p class="text-gray-500 mb-2 font-bold text-sm ">Synonyms:</p>
            <p>
              <span
                class="mr-1 my-1 text-gray-800 border-gray-300 inline-block border-2 rounded-full px-4 py-1"
                v-for="(synonym, index) in synonyms"
                :key="index"
                >{{ synonym }}</span
              >
            </p>
          </div>
        </div>
        <input
          type="text"
          placeholder="Type spelling here"
          class="px-6 py-2 my-10 w-full text-xl  shadow rounded focus:outline-none focus:ring-5 focus:ring-gray-500"
          v-model="spelling"
          @keyup="check"
          :disabled="revealed"
        />
        <div class="flex justify-between mb-10">
          <button
            class="px-4 py-1 mr-1 rounded w-1/2 bg-blue-500 text-white font-bold"
            @click="clue"
            v-if="!revealed"
            :disabled="cluesNumber === 0"
          >
            ⭐ Clue ({{ cluesNumber }})
          </button>
          <button
            class="px-4 py-1 ml-1 rounded w-1/2 bg-red-400 text-white font-bold"
            @click="surrender"
            v-if="!revealed"
          >
            🚩 Surrender
          </button>
          <button
            class="px-4 py-1 ml-1 rounded w-full bg-green-500 text-white font-bold"
            @click="loadNewWord"
            v-if="revealed"
          >
            {{ correctMessage }}⏭️ Click to proceed
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import confetti from "canvas-confetti";
export default {
  name: "App",
  data() {
    return {
      loading: false,
      word: "",
      definition: "",
      synonyms: [],
      numberOfLetters: "",
      spelling: "",
      wordHidden: "",
      wordArray: [],
      revealed: false,
      cluesNumber: 0,
      correctMessage: "",
      correct: false,
    };
  },
  methods: {
    speak(word) {
      let msg = word;
      let speech = new SpeechSynthesisUtterance();
      speech.lang = "en-US";
      speech.text = msg;
      speech.volume = 1;
      speech.rate = 1;
      speech.pitch = 1;
      window.speechSynthesis.speak(speech);
    },
    check() {
      if (this.spelling.toLowerCase() === this.word.toLowerCase()) {
        this.wordHidden = this.word.split("");
        this.revealed = true;
        this.correctMessage = "😃 Very Good!";
        this.correct = true;
        confetti({
          particleCount: 200,
          spread: 200,
        });
      }
    },
    checkCluesNumber() {
      if (
        this.word.length === 0 ||
        this.word.length === 1 ||
        this.word.length === 2 ||
        this.word.length === 3
      ) {
        this.cluesNumber = 0;
      } else if (this.word.length === 4) {
        this.cluesNumber = 1;
      } else if (this.word.length === 5 || this.word.length === 6) {
        this.cluesNumber = 2;
      } else if (this.word.length === 7 || this.word.length === 8) {
        this.cluesNumber = 3;
      } else if (this.word.length === 9 || this.word.length === 10) {
        this.cluesNumber = 4;
      } else if (this.word.length === 11 || this.word.length === 12) {
        this.cluesNumber = 5;
      } else {
        this.cluesNumber = Math.round(this.word.length / 2);
      }
    },
    clue() {
      let randomNum = Math.floor(Math.random() * this.wordArray.length);
      while (
        this.wordArray[randomNum] === "" ||
        this.wordArray[randomNum] === " "
      ) {
        randomNum = Math.floor(Math.random() * this.wordArray.length);
      }
      this.wordHidden[randomNum] = this.wordArray[randomNum];
      this.wordArray[randomNum] = "";
      this.cluesNumber--;
    },
    surrender() {
      this.wordHidden = this.word.split("");
      this.revealed = true;
    },
    async loadNewWord() {
      this.spelling = "";
      this.correctMessage = "";
      this.revealed = false;
      this.wordHidden = "";
      this.fetchRandomWord().then((randomWord) => {
        this.definition = randomWord.results[0].definition;
        this.word = randomWord.word;
        const synonyms =
          (randomWord.results?.length && randomWord.results[0].synonyms) || [];
        this.wordHidden = new Array(randomWord.word.length);
        this.wordArray = randomWord.word.split("");
        this.checkCluesNumber();
        this.synonyms = synonyms.slice(0, 5);
      });
    },
    async fetchRandomWord() {
      this.loading = true;
      const url = `https://wordsapiv1.p.rapidapi.com/words/?letters=${this.numberOfLetters}&random=true&hasDetails=definitions`;
      const options = {
        headers: {
          "x-rapidapi-key":
            "fa03bc319cmsh84c9a1f6576ed63p105974jsnbd1e35e4ba8b",
          "x-rapidapi-host": "wordsapiv1.p.rapidapi.com",
        },
      };
      const response = await fetch(url, options);
      const randomWord = await response.json();
      this.loading = false;
      return randomWord;
    },
  },
  async created() {
    await this.loadNewWord();
  },
};
</script>

<style>
body {
  @apply bg-purple-50;
}
.btn {
  @apply w-full px-4 py-2 m-2 bg-green-600 text-white font-bold rounded;
}
.speak-btn {
  font-size: 9rem;
  @apply mt-16 z-10 text-gray-600    flex items-center justify-center  focus:outline-none hover:shadow-none hover:text-purple-800;
}
.text-container {
  @apply h-8 w-8 md:h-12 md:w-12 mx-1 md:text-xl border-b-2 border-purple-800 flex justify-center items-center font-bold text-gray-800;
}
.word-description-container {
  min-height: 200px;
}
</style>
